.page {
  font-family: "Inter", sans-serif;
  scroll-behavior: smooth;

  .page__body--with-menu {
    overflow: hidden;
  }

  &__body {
    margin: 0;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    overflow: hidden;

    transform: translateX(100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 120px;

    &-title {
      margin: 0 0 48px;

      font-weight: 700;
      font-size: 32px;
      line-height: 32px;
      letter-spacing: -1.5px;
      text-align: center;

      color: $text-color;

      @include onTablet {
        margin-bottom: 64px;

        font-size: 40px;
        line-height: 100%;
      }

      @include onDesktop {
        margin-bottom: 64px;

        font-size: 48px;
        line-height: 48px;
      }
    }
  }
}
